
  import {LockOutlined, MailOutlined, PhoneOutlined, UserOutlined} from '@ant-design/icons-vue';
  import {defineComponent, onBeforeUnmount, ref} from 'vue';
  import {registerAntd} from '../../../../schema-form';

  registerAntd();
  export default defineComponent({
    name: 'Demo',
    components: {
      UserOutlined,
      LockOutlined,
      PhoneOutlined,
      MailOutlined
    },
    props: {
      size: String
    },
    setup() {
      const timer = ref(null);
      onBeforeUnmount(() => {
        if (timer.value) {
          clearInterval(timer.value);
          timer.value = null;
        }
      });
      return {
        schema2: {
          fields: {
            username: {
              title: '',
              type: 'string',
              placeholder: '手机号',
              required: true,
              slots: {
                prefix: 'usernamePrefix'
              }
            },
            $grid: {
              type: 'grid',
              layout: [16, 8],
              props: {
                gutter: 16
              },
              fields: {
                password: {
                  title: '',
                  type: 'string',
                  required: true,
                  placeholder: '验证码',
                  slots: {
                    prefix: 'passwordPrefix'
                  }
                },
                $submit: {
                  id: 'code',
                  type: 'button',
                  wrapperProps: {noTitle: true},
                  title: '获取验证码',
                  props: {
                    block: true,
                    action: ($) => {
                      $('#code').setFieldProps({
                        loading: true,
                        title: '发送中'
                      });
                      let time = 60;
                      timer.value = setInterval(() => {
                        $('#code').setFieldProps({
                          loading: true,
                          title: time + '秒'
                        });
                        time--;
                      }, 1000);
                    }
                  }
                }
              }
            }
          }
        },
        schema: {
          props: {
            size: 'large',
            okProps: {
              block: true
            }
          },
          fields: {
            username: {
              title: '',
              type: 'string',
              slots: {
                prefix: 'usernamePrefix'
              }
            },
            password: {
              title: '',
              type: 'password',
              slots: {
                prefix: 'passwordPrefix'
              }
            },
            submit: {
              type: 'button',
              title: '',
              props: {
                title: '提交',
                block: true,
                action: () => {
                  console.log(1);
                }
              }
            }
          }
        }
      };
    }
  });
